@import 'vars';

.oi {
    font-size: smaller;
}

.font-larger {
    font-size: larger;
}

select {
    /* for Firefox */
    -moz-appearance: none;
    /* for Chrome */
    -webkit-appearance: none;

    ::-ms-expand {
        display: none;
    }
}

.disabled {
    background-color: $disabled-background-color;
}

.room-allocation-info-button,
.room-allocation-calendar-button {
  width: 1.875rem;
  height: auto;
  aspect-ratio: 1/1;
}
